body {
    position: absolute;
    top:0;
    left:0;
    padding: 0;
    margin: 0;
    color: black;
    font-family: helvetica, arial;
}

.coverLogo {
    position: fixed;
    top: 30%;
    left: 10%;
    width: 300px;
}

#burgercontainer {
    position: fixed;
    top: 20px;
    right: 20px;
    height: 80px;
    width: 100px;
    z-index: 110;
    :hover {
        cursor:pointer;
    }
    svg {
        position: absolute;
        top: 0;
        left: 0;
    }
    #burgerx {
        display: none;
    }
}

nav {
    position: fixed;
    display: none;
    z-index: 100;
    background-color: #ccc;
    width: 100%;
    height: 30%;
    text-align: center;
    .link {
        position: relative;
        top: 2em;
        margin: 1em;
        font-weight: bolder;
        padding: 1em;
        font-size: 2em;
        display: inline-block;
        color: black;
        text-decoration: none;
        box-sizing: border-box;
    }
    .link:hover {
        border-bottom: solid black 5px;
    }
    .activelink {
        color: #fff;
    }
    #navlogo{
        position: absolute;
        left: 2em;
        top: 2em;
        display: inline-block;
        object {
            width: 150px;
            pointer-events: none;
        }
    }
}

#loader {
    position: fixed;
    display:none;
    top:0;
    left:0;
    height: 100%;
    width: 100%;
    background-color: #ccc;
}

    



// CONTENT

// Index
#coverBg {
    position: fixed;
    z-index:-10;
    width: 100%;
    height: 100%;
    background-image: url('../media/cover.jpg');
    background-attachment: fixed;
    background-size: 100% auto;
    background-repeat: no-repeat;
}

#coverfooter {
    position: fixed;
    color: #fff;
    z-index: 10;
    width: 100%;
    bottom: 0.5em;
    text-align: center;
    .clogo {
        display: inline-block;
        vertical-align: middle;
        width: 2em;
        margin: 1em;
    }
    span {
        display: block;
        font-size: 0.75em;
    }
}

.loader {
  position: fixed;
  background-color: #FFF;
  opacity: 1;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}

.loaderlogo {
    position: absolute;
    height: 200px;
    width: 200px;
    top: 50%;
    left: 50%;
    z-index: 11;
    margin-left: -100px;
    text-align: center;
}